import {useNavigate} from "react-router-dom";
import React, {useEffect, useState} from "react";
import {calcPremium, sendJoin} from "../../pages/api/sendJoin";
import Modal from "react-modal";
import {InfoProps} from "./inputs/carNumInput";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../pages/reducers";
import {calculateInsAge} from "../../pages/api/calculateInsAge";
import {USER_BIRTH, userBirth} from "../../pages/reducers/userInfo";
import PhoneIcon from '../../assets/images/phoneIcon.png';
const RegisterButton = (props : {info : InfoProps, setStep : React.Dispatch<React.SetStateAction<string>>} ) => {
    const page = useNavigate();
    const dispatch = useDispatch();
    const isMobile = (/Mobi/i.test(window.navigator.userAgent));
    const [msg, setMsg] = useState<string>('');
    const [flag, setFlag] = useState<boolean>(false);
    const [errMsg, setErrMsg] = useState<boolean>(false);
    const  keys : string     = useSelector((state : RootState ) => state.userInfo.keys);
    const mobi_customStyles = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            left: "50%",
            margin: "auto",
            width: "350px",
            height: "350px",
            padding: "0",
            overflow: "hidden",
            transform : "translate(-50%,0)"
        },
    };
    const customStyles = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            left: '0',
            margin: "auto",
            width: "350px",
            height: "350px",
            padding: "0",
            overflow: "hidden",
            //transform : "translate(-50%,0)"
        },
    };
    const mobi_customStyles2 = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            left: "50%",
            margin: "auto",
            width: "350px",
            height: "220px",
            padding: "0",
            overflow: "hidden",
            transform : "translate(-50%,0)"
        },
    };
    const customStyles2 = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            left: '0',
            margin: "auto",
            width: "350px",
            height: "220px",
            padding: "0",
            overflow: "hidden",
            //transform : "translate(-50%,0)"
        },
    };
    const onClickHandler = (e : React.MouseEvent<HTMLElement>) => {
        let {id}: any = e.target;
        let text = '';
        if (id === 'reg') {
            let idx: keyof typeof props.info;
            let check: string = '';
            if (keys === 'onna' || keys === 'enuri' || keys === 'barico' || keys === 'none') {
                for (idx in props.info) {
                    console.log(idx);
                    if (props.info[idx] === '' || props.info[idx] === false) {
                        check = idx;
                        console.log(props.info[idx])
                        console.log(check)
                        if (check === 'cName') text = ' .';
                        if (check === 'cJumin') text = ' .';
                        if (check === 'cCell') text = ' .';
                        if (check === 'cCheck') text = ' ,  .';
                        break;
                    }
                    /*if(props.info[idx] === '' && idx !== 'cMail' && props.info[idx] !== 'Y'){
                        check = idx;
                        if(check === 'bpk') text = ' .';
                        if(check === 'cName') text = ' .';
                        if(check === 'cCell') text = ' .';
                        if(check === 'cJumin') text = ' .';
                        if(check === 'cPost') text = ' .';
                        if(check === 'cAddr1') text = '1 .';
                        if(check === 'cAddr2') text = '2 .';
                        if(check === 'cJobN') text = ' .';
                        if(check === 'cCarCc') text = ' .';
                        if(check === 'cUseN') text = ' .';
                        if(check === 'cJobLocal') text = ' .';
                        if(check === 'cMoney') text = ' .';
                        if(check === 'cDrink') text = ' .';
                        if(check === 'cWeekD') text = ' .';
                        if(check === 'cOneD') text = ' .';
                        if(check === 'cSmoke') text = ' .';
                        if(check === 'cWeekS') text = ' .';
                        if(check === 'cOneS') text = ' .';
                        if(check === 'cHeight') text = ' .';
                        if(check === 'cWeight') text = ' .';
                        if(check === 'cBank') text = ' .';
                        if(check === 'cAccount') text = ' .';
                        if(check === 'cPayDt') text = '  .';
                        break;
                    }*/
                }
                if (check === '') {
                    let param = {
                        bpk: keys,
                        cName: props.info.cName,
                        cJumin: props.info.cJumin,
                        cCell: props.info.cCell,
                        cCheck: props.info.cCheck
                    }
                    sendJoin(param)
                        .then((res: any) => {
                            console.log(res)
                            if (res.status === 200) {
                                setFlag(true);
                                /*alert("!\n     . \n      ")*/
                            } else {
                                setMsg(res.msg)
                                setErrMsg(true);
                                /*set(res.msg);*/
                            }
                        })
                } else {
                    alert(text);
                }
            } else {
                text = 'URL  .';
                alert(text);
            }
        }
            if (id === 'confirm') {
                let birth = props.info.cJumin.substring(0,6);
                let male = '';
                let name = props.info.cName;
                if(Number(birth.substring(0,1)) >= 1){
                    birth = "19"+birth;
                }else {
                    birth = '20'+birth;
                }
                if(props.info.cJumin.substring(6,7) === '1' || props.info.cJumin.substring(6,7) === '3'){
                    male = 'm';
                }
                if(props.info.cJumin.substring(6,7) === '2' || props.info.cJumin.substring(6,7) === '4') {
                    male = 'fe';
                }
                let age = calculateInsAge(birth);
                let param = {
                    bpk : keys,
                    age : age
                }
                calcPremium(param)
                    .then((res: any) => {
                        console.log(res)
                        if (res.status === 200) {
                            let highpremiums : string = res.highpremiums;
                            let defaultpremiums : string = res.defaultpremiums;
                            /*setFlag(true);*/
                            dispatch(userBirth(birth, male, age, name, defaultpremiums, highpremiums ));
                            props.setStep('step2');
                            /*alert("!\n     . \n      ")*/
                        } else {
                            //setFlag(true);
                            alert(res.msg);
                        }
                    })
            }
    }
    useEffect(() => {

    }, [props.info]);
    return (
        <div className='w-full '>
            <div className='border  mx-8 mb-6  py-[10px] px-4 rounded-lg bg-[#0e47a1] text-white font-bold text-[14px] flex items-center justify-center'>
                <button id={'reg'} className='w-full h-full' onClick={onClickHandler}></button>
            </div>
            {   flag &&
                    <Modal isOpen={flag} style={isMobile ? mobi_customStyles : customStyles}>
                        <div className='border-b bg-[#0e47a1] text-white border-b-[#0e47a1] font-bold'>
                            <div className='flex justify-center py-2'>
                                  
                            </div>
                        </div>
                        <div className='flex-col justify-center text-[14px] font-semibold mx-4'>
                            <div className='pt-4'>
                                {props.info.cName}       .
                            </div>
                            <div className='pt-2'>
                                      (1877-3006)      .
                            </div>
                            <div className='pt-2'>
                                       .
                            </div>
                            <div>
                                 .
                            </div>
                        </div>
                        <div className=' w-full flex justify-center py-4 '>
                            <button id={'confirm'} className='button_on' onClick={onClickHandler}></button>
                        </div>
                    </Modal>
            }
            {   errMsg &&
                <Modal isOpen={errMsg} style={isMobile ? mobi_customStyles2 : customStyles2}>
                    <div className='border-b bg-[#0e47a1] text-white border-b-[#0e47a1] font-bold'>
                        <div className='flex justify-center py-2'>
                            
                        </div>
                    </div>
                    <div className='flex-col justify-center text-[14px] font-semibold mx-4 mt-2'>
                        <h2>
                            {msg}
                        </h2>
                    </div>
                    <div className='flex items-center w-full mt-8 '>
                        <div className='basis-1/2 flex justify-center'>
                            <button id={'confirm'} className='button_on' onClick={(e : React.MouseEvent<HTMLElement>)=>setErrMsg(false)}></button>
                        </div>
                        <div className='basis-1/2 flex justify-center'>
                            <button id={'confirm'} className='button_on' onClick={onClickHandler}> </button>
                        </div>
                    </div>
                </Modal>
            }
        </div>
    )
}
export default RegisterButton;
