import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux";
import {RootState} from "../../pages/reducers";

const CalcText: React.FC = () => {
    const state : any     = useSelector((state : RootState ) => state.userInfo);
    const isMobi = /Mobi/i.test(window.navigator.userAgent);
    const [info, setInfo] = useState({
        name : '',
        text : '',
        male : '',
        birth : '',
        age : '',
        defaultpremiums : "",
        highpremiums : ""
    })
    useEffect(() => {
            window.scrollTo(0,0);
            let cMale = '';
            let cBirth = '';
            let text = ''
            if (state.male === 'm') cMale = '()';
            if (state.male === 'fe') cMale = '()';
            if (state.keys === 'onna') text = '';
            if (state.keys === 'enuri') text = '';
            if (state.keys === 'barico') text = '';
            if (state.birth !== '') cBirth = state.birth.substring(0, 4) + '.' + state.birth.substring(4, 6) + '.' + state.birth.substring(6, 8);

            setInfo((prevState) => {
                return {
                    ...prevState,
                    name: state.name,
                    age: state.age,
                    male: cMale,
                    text: text,
                    birth: cBirth,
                    defaultpremiums: state.defaultpremiums,
                    highpremiums: state.highpremiums
                }
            })
    }, []);

    return (
        <div className={isMobi ? 'flex flex-col items-center justify-center pt-[120px] mx-6 pb-[90px]' : 'web_calc_1'}>
            <div className={isMobi ? 'text-[12px]  px-8 flex-col flex items-center font-semibold fixed top-[120px] bg-white w-full' : 'px-2 text-[12px] mt-4 font-semibold'}>
                <div className='flex justify-center flex-col items-center'>
                    <h2>{info.text} {info.name}  </h2>
                    <h2>    .</h2>
                    <h2>    .</h2>
                </div>
                <div className='mt-4 py-4 border-t-2 border-b-2 flex w-full font-semibold text-[12px]'>
                    <div className='basis-2/4 pl-6'>
                        <div>
                            {info.birth} {info.male} {info.age} 
                        </div>
                        <div className='mt-1'>
                            
                        </div>
                    </div>
                    <div className='basis-2/4 flex-col  items-center flex'>
                        <div className='w-full flex justify-center'>
                            <div>
                                20 / 20
                            </div>
                        </div>
                        <div className='flex mt-4 w-full'>
                            <div className='flex justify-center basis-1/2'>
                                
                            </div>
                            <div className='flex justify-center basis-1/2'>
                                
                            </div>
                        </div>
                        <div className='flex mt-4 w-full'>
                            <div className='flex justify-center basis-1/2'>
                                {info.defaultpremiums}
                            </div>
                            <div className='flex justify-center basis-1/2'>
                                {info.highpremiums}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={isMobi ? 'pt-[200px] py-2  flex w-full font-semibold text-[12px]' : ' py-2  flex w-full font-semibold text-[12px]'}>
                <div className='basis-2/4 pl-6'>
                    <div>
                           
                    </div>
                    <div className='mt-4'>
                            
                    </div>
                    <div className='mt-4'>
                         ()
                    </div>
                    <div className='mt-4'>
                         ()
                    </div>
                    <div className='mt-4'>
                        <br/>
                        (,6)
                    </div>
                    <div className='mt-4'>
                        
                    </div>
                    <div className='mt-4'>
                        
                    </div>
                    <div className='mt-4'>
                        <br/>(180)
                    </div>
                    <div className='mt-4'>
                        ()
                    </div>
                    <div className='mt-4'>
                          
                    </div>
                    <div className='mt-4'>
                           
                    </div>
                </div>
                <div className='basis-1/4 flex-col  items-center flex'>
                    <div>
                        100
                    </div>
                    <div className='mt-4'>
                        1,000
                    </div>
                    <div className='mt-4'>
                        3,000
                    </div>
                    <div className='mt-4'>
                        500
                    </div>
                    <div className='mt-6'>
                        500
                    </div>
                    <div className='mt-6'>
                        3,000
                    </div>
                    <div className='mt-[17px]'>
                        2,000
                    </div>
                    <div className='mt-4'>
                        1
                    </div>
                    <div className='mt-4'>
                        -
                    </div>
                    <div className='mt-4'>
                        -
                    </div>
                    <div className='mt-4'>
                        -
                    </div>
                </div>
                <div className='basis-1/4 flex-col  items-center flex'>
                    <div>
                        100
                    </div>
                    <div className='mt-4'>
                        1,000
                    </div>
                    <div className='mt-4'>
                        3,000
                    </div>
                    <div className='mt-4'>
                        500
                    </div>
                    <div className='mt-6'>
                        500
                    </div>
                    <div className='mt-6'>
                        3,000
                    </div>
                    <div className='mt-[17px]'>
                        3,000
                    </div>
                    <div className='mt-4'>
                        2
                    </div>
                    <div className='mt-4'>
                        10
                    </div>
                    <div className='mt-4'>
                        10
                    </div>
                    <div className='mt-4'>
                        30
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CalcText;
